import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';

import { InlineSVGModule } from 'ng-inline-svg';

import { PagesModule } from '../pages.module'

import { AutheComponent } from './authe/authe.component';
import { AuthoComponent } from './autho/autho.component';
import { AccComponent } from './acc/acc.component';
import { ReportsComponent } from './reports.component';
import { ApiComponent } from './api/api.component';
import { CmComponent } from './cm/cm.component';

@NgModule({
  declarations: [AutheComponent, AuthoComponent, AccComponent, ReportsComponent, ApiComponent, CmComponent],
  imports: [
    CommonModule,
    PagesModule,
    InlineSVGModule,
    RouterModule.forChild([
      {
        path: '',
        component: ReportsComponent
      },
      {
        path: 'authe',
        component: AutheComponent
      },
      {
        path: 'autho',
        component: AuthoComponent
      },
      {
        path: 'acc',
        component: AccComponent
      },
      {
        path: 'gui',
        component: ApiComponent
      },
      {
        path: 'cm',
        component: CmComponent
      },
    ]),
  ]
})
export class ReportsModule { }
